.backdrop {
  width: 100%;
  height: 100%;
  z-index: 100;
  background-color: rgba(94, 99, 99, .7);
  justify-content: center;
  align-items: center;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

.backdrop.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

.backdrop__btn {
  width: 34px;
  height: 34px;
  cursor: pointer;
  background-color: #fff;
  border: 1px solid rgba(0, 0, 0, .1);
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  transition: transform .25s;
  display: flex;
  position: absolute;
  top: 10px;
  right: 10px;
  box-shadow: 0 4px 4px rgba(0, 0, 0, .06);
}

.backdrop.is-hidden .backdrop__modal {
  opacity: 0;
  transform: translate(-50%, -50%)scale(.3)rotate(1turn);
}

.backdrop__modal {
  transform: translate(-50%, -50%) scale(1) rotate(0);
  max-width: 320px;
  width: 100%;
  height: 449px;
  opacity: 1;
  background-color: #ffe550;
  border-radius: 30px;
  padding: 32px 20px;
  position: absolute;
  top: 50%;
  left: 50%;
  box-shadow: 0 4px 4px rgba(0, 0, 0, .06);
}

